@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap);
.createAccount {
	font-weight: 700;
}
.cursor-pointer {
	cursor: pointer;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}
.searchBox .input-group {
	width: 360px;
}
@media screen and (max-width: 900px) {
	.searchBox .input-group {
		width: 300px;
	}
}
.searchBox input {
	background: #f2f2f2;
	color: #5c5c5c;
	border-radius: 21px;
	box-shadow: none !important;
	border: none;
	font-weight: 500;
	font-size: 14px;
	line-height: 18px;
	padding: 13px;
	padding-left: 0;
}
.searchBox .input-group-text {
	box-shadow: none !important;
	border: none;
	background: #f2f2f2;
	color: #5c5c5c;
	border-radius: 21px;
}
.searchBox input:focus {
	background: #f2f2f2;
}

.name {
	color: black;
}

.modal-header {
	background: #effff4;
	font-weight: 500;
	color: #008a45;
	border: none;
	font-size: 14px;
	justify-content: center;
}

.modal-dialog input {
	box-shadow: none !important;
	border-radius: 0;
	background: #f7f8fa;
	padding: 10px 12px;
}

.input-group-text {
	border-radius: 0;
	background: #f7f8fa;
	border-left: none;
}

#password {
	border-right: none;
}

#firstName {
	border-radius: 4px 0 0 0;
}

#lastName {
	border-radius: 0 4px 0 0;
}

#ConfirmPassword {
	border-radius: 0 0 4px 4px;
}

.modal-dialog input:focus {
	background: #f7f8fa;
}

.modal-dialog {
	max-width: 736px !important;
}

.modalCloseBtn {
	position: absolute;
	right: -10px;
	top: -25px;
	box-shadow: none !important;
	background-color: #ffffff;
	color: transparent;
	border-radius: 50%;
}

.createBtn {
	width: 100%;
	background: #2f6ce5;
	margin: 10px 0;
	border-radius: 50px;
	color: #ffffff;
}

.createBtn:hover {
	background: #2a61cf;
	color: #ffffff;
}

.socialLogin {
	width: 100%;
	border: 1px solid #d9d9db;
	margin: 5px 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.logInBtn {
	border: none;
	outline: none;
	background: none;
	font-weight: 500;
}

.upperText {
	font-size: 13px;
}

.signInFooter {
	font-size: 11px;
}

#logInEmail {
	border-radius: 4px 4px 0 0;
}

#logInPassword {
	border-radius: 0 0 0 4px;
	border-right: none;
}

.logInPassword-goup {
	border-radius: 0 0 4px 0;
	border-left: none;
}

.forgetPassword {
	width: 100%;
	font-weight: 500;
}

.forgetPassword:hover {
	color: rgba(0, 0, 0, 0.651);
}

/* Main Font  */

body {
	font-family: "IBM Plex Sans", sans-serif;
}
a {
	text-decoration: none;
}

/* for mobile pen icon */
.mobilePostBtn {
	width: 54px;
	height: 54px;
	background: linear-gradient(180deg, #ff5c5c 0%, #f0568a 100%);
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18), 0px 0px 2px rgba(0, 0, 0, 0.08);
	border-radius: 50%;
	position: fixed;
	right: 18px;
	bottom: 18px;
	color: #ffffff;
	z-index: 5;
}

.hero::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #000000;
	opacity: 0.2;
	z-index: 1;
}
.hero {
	background: url(/ATG-World/static/media/hero-bg.0b2dfdb7.png) no-repeat center center/cover;
	height: 400px;
	z-index: 2;
}
.hero .mainHeading {
	font-weight: bold;
	font-size: 36px;
}
.hero .subHeading {
	font-size: 18px;
}
.hero .headings {
	position: absolute;
	bottom: 80px;
	left: 0;
	z-index: 2;
}

/* for small screens */
.smallScreen {
	z-index: 3;
	position: absolute;
	top: 0;
	width: 100%;
}
.backBtn {
	padding: 6px 8px;
}
.joinGroupBtn {
	border: 1px solid #fff;
	padding: 6px 8px;
}

@media screen and (max-width: 768px) {
	.hero .mainHeading {
		font-size: 20px;
	}
	.hero .subHeading {
		font-size: 12px;
	}
	.hero .headings {
		left: 30px;
	}
	.hero {
		height: 300px;
	}
	.hero .headings {
		bottom: 20px;
	}
}

.postCategories button {
	color: #8a8a8a;
}
.postCategories button:hover {
	color: #000000;
}
.nav-link {
	padding: 0.5rem 12px !important;
}
.nav-link:focus {
	color: #8a8a8a;
}
.nav-link.active {
	color: #000000 !important;
	background: none !important;
	position: relative;
	transition: all 0.3s;
}
.nav-link.active::after {
	content: "";
	background: #000000;
	height: 1.5px;
	width: 100%;
	position: absolute;
	bottom: -10px;
	left: 0;
}
@media screen and (max-width: 768px) {
	.border-bottom {
		border: none !important;
	}
}

.writePostButton,
.groupJoinButton,
.mobileFilter {
	box-shadow: none !important;
	border-radius: 4px;
	padding: 8px 12px;
	margin: 8px;
	font-weight: 500;
	font-size: 14px;
}
.writePostButton,
.mobileFilter {
	background: #edeef0;
}
.groupLeaveButton {
	font-size: 14px;
	background: #ffffff !important;
	box-shadow: none !important;
	border-radius: 4px;
	padding: 8px 12px;
	margin: 8px;
	font-weight: 500;
	color: #6a6a6b !important;
	border-color: #989899;
}

/* Mobile */
.mobileFilter {
	font-weight: 500;
}

.post-category {
	font-weight: 500;
	font-size: 18px;
}
.optionsBtn {
	padding: 8px 13px;
}
.optionsBtn:focus {
	background: #bfc0c0;
	border-radius: 4px;
}

.btn {
	box-shadow: none !important;
}
.views {
	color: #525252;
	margin-right: 40px;
}
.share {
	background: #edeef0;
	border-radius: 2px;
	color: #2d2d2d;
}

.webLink {
	width: 100%;
	border: 1px solid #a9aeb8;
	border-radius: 8px;
	text-align: center;
	font-weight: 600;
}
.webLink-meetup {
	color: #e56135;
}
.webLink-meetup:hover {
	color: #e56135;
	background: #ebe3df;
}
.webLink-job {
	color: #02b875;
}
.webLink-job:hover {
	color: #02b875;
	background: #d7f0e0;
}

.locationForm input {
	box-shadow: none !important;
	border: none;
	border-bottom: 1px solid #b8b8b8;
	padding: 10px 7px;
	padding-right: 30px;
}
.locationForm input:focus {
	border-color: #b8b8b8 !important;
}
.locationForm .input-group-text {
	background: none;
	border: none;
	border-bottom: 1px solid #b8b8b8;
	padding: 0;
	border-radius: 0;
}
.input-group-text-extra {
	position: relative;
	right: 25px;
	top: 5px;
	z-index: 3;
}
.warning-text {
	font-size: 13px;
}

.heading {
	text-transform: uppercase;
	margin-bottom: 20px;
}
.followBtn {
	background: #edeef0;
	border-radius: 50px;
	padding: 2px 20px;
}
.followBtn:hover {
	background: #dddddd;
}
.followed {
	background: #000000;
	color: #ffffff;
}
.followBtn.followed:hover {
	background: #383838;
	color: #ffffff;
}

.MobileCreateAc {
	display: flex;
	flex-direction: row;
	justify-content: center;
	background: rgba(0, 0, 0, 0.6);
}
.MobileCreateAc .showModal {
	width: -webkit-max-content;
	width: max-content;
	background: #f0f0f0;
	margin: 0 10px;
}

.mobileSignUp .modalCloseBtn {
	position: absolute;
	top: 5px;
	right: 5px;
	background-color: rgba(0, 0, 0, 0.5);
	color: #fff;
	padding: 5px;
	fill: white;
	cursor: pointer;
	z-index: 3;
}
.mobileSignUp .createBtn {
	width: auto;
	width: initial;
	font-size: 13px;
}
.orSignUp {
	font-size: 14px;
	color: #495057;
	text-decoration: underline;
}

